import { Component, EventEmitter, Input, Output } from '@angular/core';
import { Plan, PlanOption } from '@youtap-web-lib';

@Component({
  selector: 'subs-card-section',
  templateUrl: './subs-card-section.component.html',
  styleUrls: ['./subs-card-section.component.scss']
})
export class SubsCardSectionComponent {
  planId = '';
  loadingPlanOption = false;
  @Input() dataPlanOption!: Plan;
  @Input() id?: string;
  @Input() planName = '';
  previousId: string | null = null;
  pendingDefaultSelection = '';
  @Input() reorder = false;
  @Input() set defaultSelect(data: string) {
    setTimeout(() => {
      if (data) {
        this.setDefaultSected(data);
      }
    }, 0);
  }
  @Output() showToast: EventEmitter<boolean> = new EventEmitter();
  planOption!: PlanOption[];
  @Output() dataPlan = new EventEmitter<PlanOption>();
  planCart!: PlanOption[];
  selectedIndices: number[] = [];
  @Output() isDeselect = new EventEmitter<boolean>();
  @Input() isSelectedRadio = false;

  constructor() {}

  setDefaultSected(id: string) {
    if (id !== this.previousId) {
      this.previousId = id;
      const itemExist = this.dataPlanOption.planOptions.filter(
        (item) => item.id === id
      );

      if (id && id !== this.id) {
        const idx = this.dataPlanOption.planOptions.findIndex(
          (data) => data.id === id
        );

        const indexBelow =
          !this.reorder || this.isSelectedRadio ? idx : idx - 1;

        if (indexBelow === -1 && !this.selectedIndices.includes(idx)) {
          this.handleClick(this.dataPlanOption.planOptions[idx], idx);
        } else if (
          indexBelow > -1 &&
          !this.selectedIndices.includes(indexBelow)
        ) {
          this.handleClick(
            this.dataPlanOption.planOptions[indexBelow],
            indexBelow
          );
        } else if (itemExist.length === 0) {
          this.selectedIndices = [];
          this.dataPlan.emit();
        }
      }
    }
  }

  handleClick(data: PlanOption, idx: number) {
    if (data) {
      data['planName'] = this.planName;
      if (this.selectedIndices.includes(idx)) {
        // User clicked a selected item, deselect it
        this.selectedIndices = this.selectedIndices.filter(
          (itemIdx) => itemIdx !== idx
        );
        this.isDeselect.emit(true);
        this.dataPlan.emit();
      } else {
        // User clicked a new item, select it
        this.selectedIndices = [idx];
        this.id = data.id;
        this.dataPlan.emit(data);
      }
    }
  }
}
