import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {BasicContainerComponent} from '../../shared/layout/container/basic-container/basic-container.component';
import {ListSubscriptionComponent} from './list-subscription/list-subscription.component';
import {HistorySubscriptionComponent} from './history-subscription/history-subscription.component';
import {
  DetailTransactionSubscriptionComponent
} from "./detail-transaction-subscription/detail-transaction-subscription.component";
import {SubscriptionResolver} from "../../shared/resolvers/subscription.resolver";

const routes: Routes = [
  {
    path: 'list-subscription',
    component: BasicContainerComponent,
    data: {canGoBack: true},
    children: [
      {
        path: '',
        resolve: {
          meta: SubscriptionResolver
        },
        component: ListSubscriptionComponent,
        title: 'Berlangganan',
        data: {
          pageTitle: 'Berlangganan'
        }
      }
    ]
  },
  {
    path: 'history',
    component: BasicContainerComponent,
    data: {canGoBack: true},
    children: [
      {
        path: '',
        component: HistorySubscriptionComponent,
        title: 'Upgrade-Akun/Riwayat',
        data: {
          pageTitle: 'Riwayat Upgrade Akun'
        }
      }
    ]
  },
  {
    path: 'detail-transaction',
    component: BasicContainerComponent,
    data: {canGoBack: true},
    children: [
      {
        path: '',
        component: DetailTransactionSubscriptionComponent,
        title: 'Upgrade-Akun/Detail-transaction',
        data: {
          pageTitle: "Detail Transaksi",
        }
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SubscriptionRoutingModule {
}
