import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { WindowActionService } from '../../../shared/service/window-action.service';
import {
  ListSubscriptionTransactionDto,
  PaymentStatusTypes,
  ResponseListSubscriptionTransactionDto,
  SubscriptionService,
  SubscriptionTransaction
} from '@youtap-web-lib';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'history-subscription',
  templateUrl: './history-subscription.component.html',
  styleUrls: ['history-subscription.component.scss']
})
export class HistorySubscriptionComponent implements OnInit {
  loadingHistory = false;
  historyData!: ResponseListSubscriptionTransactionDto;
  histories: SubscriptionTransaction[] = [];
  value = { name: '', value: PaymentStatusTypes.ALL };
  statusData = [
    {
      name: 'Semua Status',
      value: PaymentStatusTypes.ALL
    },
    {
      name: 'Menunggu Pembayaran',
      value: PaymentStatusTypes.AWAITING_PAYMENT
    },
    {
      name: 'Pesanan Selesai',
      value: PaymentStatusTypes.SUCCESS
    },
    {
      name: 'Pembayaran Gagal',
      value: PaymentStatusTypes.FAILED
    }
  ];
  showCalendar = false;
  dateModel = [];
  dateNow = new Date().toString();
  page = 1;
  loadMore = false;
  canLoad = true;
  constructor(
    private subscriptionService: SubscriptionService,
    private router: Router,
    private windowActionService: WindowActionService,
    private gaService: GaService
  ) {}

  ngOnInit() {
    this.getListHistory(this.value.value);
  }

  onChangeDropdown() {
    this.gaService.addEvent('Upgrade_akun_history_filter_status', 'subscription', '');
    this.page = 1;
    this.histories = [];
    
    switch (this.value.value) {
      case 'AWAITING_PAYMENT':
        this.gaService.addEvent('Upgrade_akun_history_filter_menunggu_pembayaran', 'subscription', '');
        break;
      case 'SUCCESS':
        this.gaService.addEvent('Upgrade_akun_history_filter_pesanan_selesai', 'subscription', '');
        break;
      case 'FAILED':
        this.gaService.addEvent('Upgrade_akun_history_filter_pembayaran_gagal', 'subscription', '');
        break;
      default:
        this.gaService.addEvent('Upgrade_akun_history_filter_semua', 'subscription', '');
        break;
    }
    this.getListHistory(this.value.value);
  }

  onChangeDate() {
    if (this.dateModel[1]) {
      this.gaService.addEvent('Upgrade_akun_history_filter_date', 'subscription', '');
      this.page = 1;
      this.histories = [];
      this.getListHistory(this.value.value);
      this.showCalendar = !this.showCalendar;
    }
  }

  clearDate() {
    this.page = 1;
    this.histories = [];
    this.dateModel = [];
    this.showCalendar = !this.showCalendar;
    this.getListHistory(this.value.value);
  }

  // handleLoadMore() {
  //   this.loadingHistory = false;
  //   this.page = this.page + 1;
  //   this.getListHistory(this.value.value);
  // }

  getListHistory(status: PaymentStatusTypes) {
    this.canLoad = true;
    this.loadingHistory = true;
    this.loadMore = true;
    const data: ListSubscriptionTransactionDto = {
      listSubscriptionTransactionInput: {
        page: this.page,
        limit: 15,
        filter: {
          ...(status !== PaymentStatusTypes.ALL && { status: status }),
          ...(this.dateModel[0] !== null && { startDate: this.dateModel[0] }),
          ...(this.dateModel[1] !== null && { endDate: this.dateModel[1] })
        }
      }
    };

    this.subscriptionService.listSubscriptionTransaction(data).subscribe({
      next: ({ data }) => {
        this.loadingHistory = false;
        this.historyData = data;
        data.listSubscriptionTransaction.subscriptionTransaction.forEach(
          (history) => {
            this.histories.push(history);
          }
        );
        if (this.page === data.listSubscriptionTransaction.totalPage) {
          this.loadMore = false;
        }
        if (
          data.listSubscriptionTransaction.subscriptionTransaction.length === 0
        ) {
          this.canLoad = false;
        }
      },
      error: () => {
        this.loadingHistory = false;
      }
    });
  }

  goToListSubscription() {
    this.router.navigate(['/subscription/list-subscription']);
  }

  goToFaqLink() {
    this.windowActionService.open(
      'https://www.youtap.id/app/cara-daftar-akun-youtap',
      '_blank'
    );
  }

  onScroll() {
    this.page += 1;
    if (
      this.canLoad &&
      this.page <= this.historyData.listSubscriptionTransaction.totalPage
    ) {
      this.getListHistory(this.value.value);
    }
  }
}
