/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { Clipboard } from '@angular/cdk/clipboard';

import {
  DetailSubscription,
  DetailSubscriptionTransactionResponse,
  GetSubscriptionInput,
  SubscriptionService,
  SubscriptionTransactionDetails
} from '@youtap-web-lib';
import { TransactionHelperService } from '../../../shared/service/transaction-helper.service';
import { DownloadSubsInvService } from '../../../shared/service/signal/download-subs-inv.signal.service';

@Component({
  selector: 'detail-transaction-subscription',
  templateUrl: './detail-transaction-subscription.component.html'
})
export class DetailTransactionSubscriptionComponent implements OnInit {
  loadingDetailSubs = false;
  id = '';
  dataSubs!: DetailSubscriptionTransactionResponse;
  dataSubsDetails!: SubscriptionTransactionDetails[];
  transactionStatus = {
    label: '',
    color: ''
  };
  downloadSubsInv: any;
  isLoading: boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private subscriptionService: SubscriptionService,
    private location: Location,
    private clipboard: Clipboard,
    private transactionHelperService: TransactionHelperService,
    private downloadSubsInvService: DownloadSubsInvService
  ) {}

  ngOnInit() {
    this.id = this.route.snapshot?.queryParamMap.get('id') ?? '';
    this.getDetailSubs();
  }

  copyToClipboard(value: string) {
    this.clipboard.copy(value);
    alert('Berhasil disalin!');
  }

  downloadInv(invoiceId: string) {
    const getSubscriptionInput: GetSubscriptionInput = {
      getSubscriptionInput: {
        invoice: invoiceId
      }
    };

    this.isLoading = true;
    this.downloadSubsInvService
      .downloadSubsInvService(getSubscriptionInput)
      .then(() => {
        this.isLoading = false;
        this.downloadSubsInv =
          this.downloadSubsInvService?.data$()?.getSubscriptionInvoice.urlDownload;
        window.open(this.downloadSubsInv, '_blank');
      })
      .catch((error) => {
        console.error('Error fetching inventory stocks:', error);
      });
  }

  getDetailSubs() {
    this.loadingDetailSubs = true;
    const data: DetailSubscription = {
      id: this.id
    };

    this.subscriptionService.detailSubscriptionTransaction(data).subscribe({
      next: ({ data }) => {
        this.loadingDetailSubs = false;

        this.dataSubs = data.detailSubscriptionTransaction;
        const voucherAmount =
          data.detailSubscriptionTransaction.priceBeforeVoucher >
          data.detailSubscriptionTransaction.total
            ? data.detailSubscriptionTransaction.priceBeforeVoucher -
              data.detailSubscriptionTransaction.total
            : 0;
        Object.assign(this.dataSubs, { voucherAmount });

        this.dataSubsDetails =
          data.detailSubscriptionTransaction.subscriptionTransactionDetails.map(
            (item) => {
              return {
                ...item,
                discountAmount: 0,
                normalPrice: 0
              };
            }
          );
        this.transactionStatus = this.transactionHelperService.convertStatus(
          data.detailSubscriptionTransaction.status
        );
        data.detailSubscriptionTransaction.subscriptionTransactionDetails.forEach(
          ({ planOption, planBasePrice, planOptionPrice }, index) => {
            let basePriceEachItem = 0;
            switch (planOption.termType) {
              case 'DAY':
                basePriceEachItem = (planBasePrice / 30) * planOption.term;
                break;
              case 'MONTH':
                basePriceEachItem = planBasePrice * planOption.term;
                break;
              case 'WEEK':
                basePriceEachItem = (planBasePrice / 4) * planOption.term;
                break;
              case 'YEAR':
                basePriceEachItem = planBasePrice * 12;
                break;
              default:
                basePriceEachItem = planBasePrice;
                break;
            }
            const discountPercentage = basePriceEachItem - planOptionPrice;

            const discount =
              basePriceEachItem > planOptionPrice
                ? Math.floor(discountPercentage)
                : 0;
            const normalPrice =
              basePriceEachItem > planOptionPrice
                ? Math.floor(basePriceEachItem)
                : planOptionPrice;
            this.dataSubsDetails[index].discountAmount = discount;
            this.dataSubsDetails[index].normalPrice = normalPrice;
          }
        );
      },
      error: () => {
        this.loadingDetailSubs = false;
      }
    });
  }
}
