import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { PaymentData, PaymentDataType } from '../../../data/payment.data';
import {
  PaymentStatusTypes,
  SubscriptionTransactionDetail
} from '@youtap-web-lib';
import { GaService } from '../../../service/ga.service';

@Component({
  selector: 'lib-history-subscription-card',
  templateUrl: './history-subscription-card.component.html'
})
export class HistorySubscriptionCardComponent implements OnInit {
  @Input() invoice = '';
  @Input() image =
    'https://storage.googleapis.com/staging-youtap-public/profile-image/UXpbTU_e_=s0.png';
  @Input() displayName = 'Self Setup Loyalty Program';
  @Input() date = '';
  @Input() transactionId = '';
  @Input() id = '';
  @Input() status = PaymentStatusTypes.SUCCESS;
  @Input() total = 10000;
  @Input() term = 5;
  @Input() termType = 'Bulan';
  @Input() transactionDetailsLength = 1;
  @Input() subscriptionTransactionDetails: SubscriptionTransactionDetail[] = [];
  planIds!: { id: string }[];

  constructor(private router: Router, private paymentData: PaymentData, private gaService: GaService) {}

  ngOnInit() {
    this.handleCheckIds();
  }

  order() {
    this.gaService.addEvent('Upgrade_akun_rebuy', 'subscription', '');
    this.router.navigate(['/subscription/list-subscription'], {
      queryParams: { id: this.id, reorder: true }
    });
  }

  handleCheckIds() {
    if (this.subscriptionTransactionDetails) {
      this.planIds = this.subscriptionTransactionDetails.map((item) => {
        return {
          id: item.id
        };
      });
    }
  }

  handleCheckPackage() {
    this.router
      .navigate(['/payment/list'], {
        queryParams: {
          invoice: this.invoice,
          type: PaymentDataType.SUBSCRIPTION
        }
      })
      .then(() => {
        this.paymentData.payment = {
          total: this.total,
          voucher: this.paymentData.payment.voucher,
          type: PaymentDataType.SUBSCRIPTION
        };

        this.paymentData.paymentSubscription = {
          planOption: this.planIds
        };
      });
  }

  goToDetailTransaction() {
    this.gaService.addEvent('Upgrade_akun_detail', 'subscription', '');
    const id = this.transactionId;
    this.router.navigate(['/subscription/detail-transaction'], {
      queryParams: {
        id
      }
    });
  }
}
