import { ActivatedRoute, Router } from '@angular/router';
import { isPlatformBrowser } from '@angular/common';
import {
  Component,
  Inject,
  OnInit,
  PLATFORM_ID,
  ViewEncapsulation
} from '@angular/core';
import {
  ListEligibleVoucherInput,
  ListEligibleVoucherResponse,
  PlanOption,
  VoucherService
} from '@youtap-web-lib';
import { WindowActionService } from '../../../shared/service/window-action.service';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { ReferralService } from '../../../shared/service/referral.service';
import { MetaService } from '../../../shared/service/meta.service';
import {
  PaymentData,
  PaymentDataType
} from '../../../shared/data/payment.data';
import { EligibleVoucherComponent } from '../../cart/payment-summary/eligible-voucher/eligible-voucher.component';
import { SubscriptionData } from '../../../shared/data/subscription.data';
import { AuthData } from '../../../shared/data/auth.data';

import {
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/animations';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'list-subscription',
  templateUrl: './list-subscription.component.html',
  encapsulation: ViewEncapsulation.None,
  animations: [
    trigger('toastAnimation', [
      state('void', style({ opacity: 0 })),
      state('*', style({ opacity: 1 })),
      transition(':enter, :leave', [animate('0.3s')])
    ])
  ]
})
export class ListSubscriptionComponent implements OnInit {
  subsPage = 1;
  planCart!: PlanOption[];
  price = 0;
  totalPrice = 0;
  dataPlanOption!: PlanOption;
  totalEligibleVouchers!: number | undefined;
  loadingVoucher = false;
  checkedTnc = false;
  showTnCModal = false;
  isShowModal = false;
  voucherValue = 0;
  couponCodeText = '';
  listVoucher!: ListEligibleVoucherResponse;
  defaultSelect = '';
  refCode!: string;
  planOptionId = '';
  showToast = false;
  reorder = false;
  isDeselect!: boolean;

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private router: Router,
    private subscriptionData: SubscriptionData,
    public authData: AuthData,
    private windowActionService: WindowActionService,
    private voucherService: VoucherService,
    private paymentData: PaymentData,
    private modalService: ModalService,
    private activatedRoute: ActivatedRoute,
    private referralService: ReferralService,
    private metaService: MetaService,
    private gaService: GaService
  ) {}

  ngOnInit() {
    this.gaService.addEvent('Subscription_page', 'subscription', '');
    this.reorder =
      this.activatedRoute.snapshot?.queryParamMap.get('reorder') === 'true';
    if (isPlatformBrowser(this.platformId)) {
      this.checkDataPlanCart();
      if (this.authData.token) {
        this.getListVoucher();
      }
      this.couponCodeText = history.state
        ? history.state['couponCodeText']
        : '';
      this.voucherValue = history.state ? history.state['voucherValue'] : '';
      this.defaultSelect =
        this.activatedRoute.snapshot?.queryParamMap.get('id') || '';

      this.refCode = this.activatedRoute.snapshot.queryParams['refcode'];
      this.setReferral();
      if (this.defaultSelect) {
        this.detailPlanOption();
      }
    }
  }

  detailPlanOption() {
    this.activatedRoute.data.subscribe(({ meta }) => {
      this.metaService.setMeta(meta);
    });
  }

  setReferral() {
    if (this.defaultSelect && this.refCode) {
      this.referralService.setReferral(
        this.defaultSelect,
        this.refCode,
        'subscription'
      );
    }
  }

  checkDataPlanCart() {
    if (this.subscriptionData.getPlanCart) {
      this.planCart = this.subscriptionData.getPlanCart;
    }
  }

  handleCheckout() {
    if (!this.authData.token) {
      this.goToLogin();
    } else {
      this.gaService.addEvent(
        'Subscription_click_button_lanjutkan_pembayaran',
        'subscription',
        ''
      );
      this.router
        .navigate(['/payment/list'], {
          queryParams: { type: PaymentDataType.SUBSCRIPTION }
        })
        .then(() => {
          this.paymentData.payment = {
            total: this.voucherValue
              ? this.dataPlanOption.price - this.voucherValue
              : this.dataPlanOption.price,
            voucher: this.couponCodeText,
            type: PaymentDataType.SUBSCRIPTION
          };
          const referral = this.referralService.findReferral(
            this.dataPlanOption.id
          )
            ? this.referralService.getReferral(this.dataPlanOption.id)?.referral
            : this.referralService.getLastReferralByType('subscription')
            ? this.referralService.getLastReferralByType('subscription')
                .referral
            : '';
          const planOptionIds: { id: string; referralCode?: string }[] = [
            {
              id: this.dataPlanOption.id,
              referralCode: referral
            }
          ];
          this.paymentData.paymentSubscription = {
            planOption: planOptionIds
          };
        });
    }
  }

  onScroll(event: Event) {
    const target = event.target as HTMLElement;
    if (target.offsetHeight + target.scrollTop >= target.scrollHeight) {
      this.subsPage = this.subsPage + 1;
    }
  }

  openWhatsapp() {
    this.gaService.addEvent(
      'Upgrade_akun_solutioning_service',
      'subscription',
      ''
    );
    this.windowActionService.open(
      'https://api.whatsapp.com/send?phone=08111500523',
      '_blank'
    );
  }

  getSelected(dataPlanOption: PlanOption) {
    this.gaService.addEvent('Subscription_plan_choose', 'subscription', '');
    this.dataPlanOption = dataPlanOption;
    if (this.authData.token) {
      this.getListVoucher();
    }
    this.voucherValue = 0;
    this.couponCodeText = '';
  }

  getListVoucher() {
    const data: ListEligibleVoucherInput = {
      listEligibleVoucherInput: {
        planOptionIds: [this.dataPlanOption ? this.dataPlanOption.id : '']
      }
    };
    this.voucherService.listEligibleVoucherSubscription(data).subscribe({
      next: ({ data }) => {
        this.loadingVoucher = false;
        this.listVoucher = data;
        this.totalEligibleVouchers =
          data.listEligibleVoucher?.availableVouchers?.length;
      },
      error: (err) => {
        this.loadingVoucher = false;
        return err;
      }
    });
  }

  showModalTnc() {
    this.gaService.addEvent(
      'Subscription_click_syarat_ketentuan',
      'subscription',
      ''
    );
    this.showTnCModal = !this.showTnCModal;
  }

  checkUnCheckTnc() {
    this.gaService.addEvent(
      'Subscription_tick_syarat_ketentuan',
      'subscription',
      ''
    );
    this.checkedTnc = !this.checkedTnc;
  }

  approvedTnc() {
    this.gaService.addEvent(
      'Subscription_click_button_saya_setuju',
      'subscription',
      ''
    );
    this.showTnCModal = false;
    this.checkedTnc = true;
  }

  showModalVoucher() {
    if (this.authData.token) {
      if (this.voucherValue) {
        this.gaService.addEvent(
          'Subscription_click_button_ubah_promo',
          'subscription',
          ''
        );
      } else {
        this.gaService.addEvent(
          'Subscription_click_voucher_section',
          'subscription',
          ''
        );
      }
      this.isShowModal = false;
      const modalRef = this.modalService.open(EligibleVoucherComponent, {
        title: '',
        closeButton: false,
        data: {
          isFromSubscription: true,
          listVoucher: this.listVoucher,
          planOptionIds: [this.dataPlanOption.id]
        }
      });
      modalRef.onResult().subscribe((value) => {
        if (value) {
          this.voucherValue = value.voucherValue;
          this.couponCodeText = value.couponCodeText;
        } else {
          this.voucherValue = 0;
          this.couponCodeText = '';
        }
      });
    } else {
      this.goToLogin();
    }
  }

  gotoHistorySubsription() {
    this.router.navigate(['/subscription/history']);
  }

  goToLogin() {
    this.router.navigate(['/login'], {
      queryParams: { stateUrl: '/subscription/list-subscription' }
    });
  }

  handleShowToast(event: boolean) {
    this.showToast = event;
  }
  handleDeselect(event: boolean) {
    this.isDeselect = event;
  }
}
