import { Component, EventEmitter, Input, Output } from '@angular/core';
import {
  ListPlanDto,
  Plan,
  PlanOption,
  SubscriptionService
} from '@youtap-web-lib';
import { ReferralService } from '../../../service/referral.service';
import { GaService } from '../../../service/ga.service';

@Component({
  selector: 'subs-section',
  templateUrl: './subs-section.component.html',
  styleUrls: ['subs-section.component.scss']
})
export class SubsSectionComponent {
  loadingPlan = false;
  isGetPlan = true;

  @Input() reorder = false;

  @Input() set page(data: number) {
    if (data && this.isGetPlan) {
      this.getListPlan(data);
    }
  }

  @Input() defaultSelect = '';
  dataPlan!: Plan[];
  showFeatureMantap: boolean[] = [];
  showSuggestions: boolean[] = [];
  @Output() planoption = new EventEmitter<PlanOption>();
  @Output() showToast = new EventEmitter<boolean>();
  filteredPlanOptions: [PlanOption[], PlanOption[], PlanOption[]] = [
    [],
    [],
    []
  ];
  tempData: PlanOption[] = [];
  @Output() isDeselect = new EventEmitter<boolean>();
  isSelectedRadio = false;
  constructor(
    private subscriptionService: SubscriptionService,
    private referralService: ReferralService,
    private gaService: GaService
  ) {}

  getListPlan(page: number) {
    this.loadingPlan = true;
    const refferall =
      this.referralService.getLastReferralByType('subscription');
    const dataPlan: ListPlanDto = {
      listPlanInput: {
        limit: 25,
        page: page
      },
      referralCode: refferall ? refferall.referral : ''
    };
    this.subscriptionService.listPlan(dataPlan).subscribe({
      next: ({ data }) => {
        this.loadingPlan = false;
        this.dataPlan = data.listPlan.plans.map((val) => {
          val.planOptions.map((plan) => {
            plan.isReferral = plan.isAffiliate;
            return plan;
          });
          return val;
        });
        // Reorder the dataPlan array based on plan names
        this.dataPlan = this.dataPlan
          .filter(({ name }) => name !== 'extra_untung')
          .sort((a) => {
            if (a.name === 'extra_mantap') {
              this.dataPlan
                .filter(({ name }) => name === 'extra_mantap')
                .map((plan) => {
                  plan.title = 'Eitss.. ada paket lainnya';
                  plan.subtitle = 'Dapatkan Extra fitur untuk loyalty..';
                });
              return -1;
              // eslint-disable-next-line no-dupe-else-if
            } else if (a.name === 'extra_mantap') {
              return 1;
            } else {
              return 0;
            }
          });
        this.showFeatureMantap = this.dataPlan.map(() => false);
        this.showSuggestions = this.dataPlan.map(() => false);

        data.listPlan.plans.map((plan) => {
          const updatedPlanOptions = plan.planOptions.map((option) => {
            let basePriceEachItem = 0;
            switch (option.termType) {
              case 'DAY':
                basePriceEachItem = (plan.basePrice / 30) * option.term;
                break;
              case 'MONTH':
                basePriceEachItem = plan.basePrice * option.term;
                break;
              case 'WEEK':
                basePriceEachItem = (plan.basePrice / 4) * option.term;
                break;
              case 'YEAR':
                basePriceEachItem = plan.basePrice * 12;
                break;
              default:
                basePriceEachItem = plan.basePrice;
                break;
            }
            const discountAmount = basePriceEachItem - option.price;

            const discount =
              basePriceEachItem > option.price ? Math.floor(discountAmount) : 0;
            const normalPrice =
              basePriceEachItem > option.price
                ? Math.floor(basePriceEachItem)
                : 0;

            return {
              ...option,
              discount,
              normalPrice
            };
          });
          plan.planOptions = updatedPlanOptions;
        });

        if (page === data.listPlan.totalPage) {
          this.isGetPlan = false;
        }
      },
      error: () => {
        this.loadingPlan = false;
      }
    });
  }

  handleShowFeature(idx: number) {
    this.gaService.addEvent(
      'Subscription_plan_click_lihat_fiturnya',
      'subscription',
      ''
    );
    this.showFeatureMantap[idx] = !this.showFeatureMantap[idx];
  }

  getDataOption(dataPlanOption: PlanOption, index: number) {
    this.tempData[index] = dataPlanOption;
    const filter = this.tempData.filter((val) => val !== undefined);
    if (dataPlanOption && filter.length) {
      this.defaultSelect = dataPlanOption.id;
      this.isSelectedRadio = true;
      this.planoption.emit(dataPlanOption);
      this.filteredPlanOptions[index] = [];
      const filteredPlanOptions = this.dataPlan[index].planOptions.filter(
        (planOption) =>
          (planOption.termType === dataPlanOption.termType &&
            planOption.term > dataPlanOption.term) ||
          (dataPlanOption.termType === 'DAY' &&
            planOption.termType !== 'DAY' &&
            planOption.term)
      );
      this.filteredPlanOptions[index].push(...filteredPlanOptions);
      this.defaultSelect = dataPlanOption.id;
    } else if (!filter.length) {
      this.planoption.emit();
    }

    if (!dataPlanOption) {
      this.filteredPlanOptions[index] = [];
    }

    if (this.dataPlan.length > 0 && index + 1 === this.dataPlan.length) {
      const toast = this.tempData.filter((val) => val !== undefined);
      if (toast.length === 0) {
        this.showToast.emit(true);
        setTimeout(() => {
          this.showToast.emit(false);
        }, 3000);
      }
    }
  }
  handleDeselect(event: boolean) {
    this.isDeselect.emit(event);
  }
}
