import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve} from '@angular/router';
import {Observable} from 'rxjs';
import {MetaTagInterface} from '../service/meta.service';
import {DetailPlanOption, SubscriptionService, TermTypesNew} from "@youtap-web-lib";

@Injectable({providedIn: 'root'})
export class SubscriptionResolver implements Resolve<MetaTagInterface> {
  constructor(
    private subscriptionService: SubscriptionService) {
  }

  resolve(route: ActivatedRouteSnapshot): | Observable<MetaTagInterface> | Promise<MetaTagInterface> | MetaTagInterface {
    const id = route.queryParams['id'];
    const data: DetailPlanOption = {
      id: id
    };
    return this.subscriptionService.detailPlanOption(data).toPromise()
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      .then(({data}: any) => {
        const meta: MetaTagInterface = {
          title: data.detailPlanOption.plan.displayName + ' ' + data.detailPlanOption.term + ' ' + this.convertDay(data.detailPlanOption.termType),
          description: "Kelola usaha dengan Mantap pakai Youtap POS",
          image: data.detailPlanOption.plan.image
        };
        return meta;
      })
      .catch(() => {
        return {
          title: 'Youtap POS',
          description: 'Kelola usaha dengan Mantap pakai Youtap POS',
          image: '',
        };
      });
  }

  convertDay(termType: string): string {
    switch (termType) {
      case TermTypesNew.DAY:
        return 'hari';
      case TermTypesNew.DAILY:
        return 'hari';
      case TermTypesNew.WEEK:
        return 'minggu';
      case TermTypesNew.WEEKLY:
        return 'minggu';
      case TermTypesNew.MONTH:
        return 'bulan';
      case TermTypesNew.MONTHLY:
        return 'bulan';
      case TermTypesNew.YEAR:
        return 'tahun';
      default:
        return '';
    }
  }
}
