import { Subject, takeUntil } from 'rxjs';
import { Injectable, computed, signal } from '@angular/core';
import {
  ResponseGetSubscriptionDto,
  InvoiceService,
  GetSubscriptionInput
} from '@youtap-web-lib';

const InitialState = {
  data$: {} as ResponseGetSubscriptionDto,
  loaded$: false,
  loading$: false,
  error$: null
};

@Injectable({ providedIn: 'root' })
export class DownloadSubsInvService {
  private terminate$ = new Subject();
  state = signal(InitialState);
  data$ = computed(() => this.state().data$);
  loaded$ = computed(() => this.state().loaded$);
  loading$ = computed(() => this.state().loading$);
  error$ = computed(() => this.state().error$);

  constructor(private invoiceService: InvoiceService) {}

  downloadSubsInvService(input: GetSubscriptionInput): Promise<true> {
    return new Promise((resolve, reject) => {
      this.state.set({
        data$: {} as ResponseGetSubscriptionDto,
        loaded$: false,
        loading$: true,
        error$: null
      });
      this.invoiceService
        .downloadSubsInv(input)
        .pipe(takeUntil(this.terminate$))
        .subscribe({
          next: ({ data }) => {
            this.state.set({
              data$: data,
              loaded$: true,
              loading$: false,
              error$: null
            });
            resolve(true);
          },
          error: (error) => {
            this.state.set({
              data$: {} as ResponseGetSubscriptionDto,
              loaded$: true,
              loading$: false,
              error$: error
            });
            reject(error);
          }
        });
    });
  }

  destroy() {
    this.terminate$.next(1);
    this.terminate$.complete();
  }
}
